import '../screens/styles/manager.css'
import './styles/addCard.css'
import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { useStore } from '../context/Context';
import { reverseWord, today, formatDateForInput } from '../utils/Utils'

function AddCardForm() {                                                     
  const {
    userName,  
    setCards,
    formData, 
    setFormData,
    fileName, 
    setFileName,
    setFile,
    setPdfURL,
    setGetCardError,
    setShowApproveModal,
    showAddition,
    setLoading,
    setHasMore,
    setPage
  } = useStore();
  useEffect(() => {
      if (userName) {
        setFormData((prev: any) => ({
          ...prev,
          author: userName,
          createDate: reverseWord(today)
        }));
      }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userName, setFormData]);
  const [fileLog, setFileLog] = useState(false);
  const GetCards = async () => {

    setLoading(true);
    setGetCardError(null);

    try {

      const res = await axios.post("http://116.202.198.11/api/get/Cards", {page: 1, limit: 50});
      const newCards = res.data.cards || [];
      setCards(newCards); //    
      setHasMore(newCards.length === 50); // true   50 
      setPage(1);

    } catch (err: any) {
      setGetCardError(err?.response?.data?.error || '   ');
    } finally {
      setLoading(false);
    }
    
  }
  useEffect(() => {
    GetCards()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[]);
  const handleFileChoose = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files?.[0];
      setFileName(file.name);
      setFile(e.target.files[0]);
      setPdfURL(URL.createObjectURL(file));
    }
  };
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    if (["docCreateDate", "docSigningDate", "validityPeriod"].includes(name)) {
      const [year, month, day] = value.split("-");
      setFormData({ ...formData, [name]: `${day}-${month}-${year}` });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  }; 
  const openApproveModal = () => {
    if(fileName){
      setShowApproveModal(true)
      setFileLog(false)
    }
    else {
      setFileLog(true)
    }
  }

  
  return (
    <>
      {showAddition ? 
        (<h1> </h1>) : 
        (<h1>  </h1>)
      }
      <div className='addCardFormInput'>

        <div className='inputsContainer'>

              <div className='addInput'>
                <h1> :</h1>
                <select 
                  name='contractType'
                  value={formData.contractType}
                  onChange={handleChange}
                  >
                  <option value=""> </option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div className='addInput'>
                <h1> :</h1>
                <input 
                  type='date' 
                  name='docCreateDate' 
                  value={formatDateForInput(formData.docCreateDate)} 
                  onChange={handleChange} 
                  placeholder=' '
                />
              </div>
              <div className='addInput'>
                <h1> :</h1>
                <input 
                  type='date' 
                  name='docSigningDate' 
                  value={formatDateForInput(formData.docSigningDate)}
                  onChange={handleChange}  
                  placeholder=' '
                />
              </div>
              <div className='addInput'>
                <h1> :</h1>
                <input 
                  type='date' 
                  name='validityPeriod'
                  value={formatDateForInput(formData.validityPeriod)}
                  onChange={handleChange} 
                  placeholder=' '
                />
              </div>
  
              </div>

              <div className='inputsContainer'>
              
              <div className='addInput' style={{height: "3vw"}}>
                <h1>:</h1>
                <textarea
                  name='name'
                  value={formData.name}
                  onChange={handleChange}
                  style={{width: "12vw", height: "3vw"}}
                  placeholder=''
                />
              </div>
              <div className='addInput' >
                <h1> :</h1>
                <input
                  name='organizationName'
                  value={formData.organizationName}
                  onChange={handleChange}
                  placeholder=' '
                />
              </div>
              <div className='addInput'>
                <h1>  :</h1>
                <input
                  name='organisationCode'
                  value={formData.organisationCode}
                  onChange={handleChange}
                  placeholder='  '
                />
              </div>
              <div className='addInput'>
                <h1>  :</h1>
                <input 
                  name='counterpartyCode' 
                  value={formData.counterpartyCode} 
                  onChange={handleChange}
                  placeholder='  '
                />
              </div>
  
              </div>
              
              <div className='inputsContainer'>
  
              <div className='addInput'>
                <h1> :</h1>
                <select 
                  name='docType' 
                  value={formData.docType} 
                  onChange={handleChange}>
                  <option value=""> </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=" "> </option>
                  <option value=" "> </option>
                </select>
              </div>
              <div className='addInput'>
                <h1> :</h1>
                <input 
                  name='docNumber'
                  value={formData.docNumber}
                  onChange={handleChange}
                  placeholder='  '
                />
              </div>
              <div className='addInput'>
                <h1> :</h1>
                <input 
                  name='counterpartyName'
                  value={formData.counterpartyName}
                  onChange={handleChange}
                  placeholder=' '
                />
              </div>
              </div>

      </div>
      <div className="choosePDFContainer">
        <input 
          id="pdfUpload" 
          type="file" 
          accept="application/pdf" 
          onChange={handleFileChoose} 
          style={{ display: "none" }} 
        />
        <label htmlFor="pdfUpload" className="custom-file-label">
           
        </label>
        <span>{fileName}</span>
      </div>
      <div className='contentContainer'>
                  <h1> :</h1>
                  <textarea
                    name='content'
                    value={formData.content}
                    onChange={handleChange}
                    placeholder=" ..."
                  
                  />
      </div> 
      {fileLog && <h1 style={{color:'red', fontSize:"1vw", margin:0, marginTop:0}}>   !</h1>}
      <div className='addButtons'>
        <button onClick={() => openApproveModal()}></button>
      </div>
    </>
  )
}

export default AddCardForm